import React, { useState, useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { 
  X, 
  CheckCircle, 
  Clock, 
  AlertTriangle, 
  Edit, 
  Trash2,
  Filter,
  Plus
} from 'lucide-react';
import { 
  fetchTasksByStaff, 
  updateTask, 
  updateTaskStatus,
  deleteTask,
  fetchProjects,
  fetchStages
} from '../store/actions/actions';
import { TaskStatusOptions } from '../types/database';

const StaffTaskModal = ({ staff, onClose }) => {
  const dispatch = useDispatch();
  const [tasks, setTasks] = useState([]);
  const [filteredTasks, setFilteredTasks] = useState([]);
  const [projects, setProjects] = useState([]);
  const [stages, setStages] = useState([]);
  const [loading, setLoading] = useState(true);
  const [filterStatus, setFilterStatus] = useState('all');
  const [sortBy, setSortBy] = useState('status'); // status, project, stage
  const [editingTask, setEditingTask] = useState(null);

  useEffect(() => {
    loadData();
  }, [staff.id, dispatch]);

  useEffect(() => {
    applyFilters();
  }, [tasks, filterStatus, sortBy]);

  const loadData = async () => {
    setLoading(true);
    try {
      const [tasksResult, projectsResult, stagesResult] = await Promise.all([
        dispatch(fetchTasksByStaff(staff.id)),
        dispatch(fetchProjects()),
        dispatch(fetchStages())
      ]);

      if (tasksResult.success) {
        setTasks(tasksResult.data || []);
      }
      if (projectsResult.success) {
        setProjects(projectsResult.data || []);
      }
      if (stagesResult.success) {
        setStages(stagesResult.data || []);
      }
    } catch (error) {
      console.error('Error loading staff tasks:', error);
    } finally {
      setLoading(false);
    }
  };

  const applyFilters = () => {
    let filtered = [...tasks];

    // Filtrar por estado
    if (filterStatus !== 'all') {
      filtered = filtered.filter(t => t.status === filterStatus);
    }

    // Ordenar
    switch (sortBy) {
      case 'status':
        filtered.sort((a, b) => {
          const statusOrder = ['Bloqueado', 'Pendiente', 'En Dise√±o', 'En Progreso', 'Aprobaci√≥n Requerida', 'Completo'];
          return statusOrder.indexOf(a.status) - statusOrder.indexOf(b.status);
        });
        break;
      case 'project':
        filtered.sort((a, b) => {
          const projA = getProjectName(a.project_id);
          const projB = getProjectName(b.project_id);
          return projA.localeCompare(projB);
        });
        break;
      case 'stage':
        filtered.sort((a, b) => {
          const stageA = getStageName(a.stage_id);
          const stageB = getStageName(b.stage_id);
          return stageA.localeCompare(stageB);
        });
        break;
      default:
        break;
    }

    setFilteredTasks(filtered);
  };

  const getProjectName = (projectId) => {
    const project = projects.find(p => p.id === projectId);
    return project ? project.name : 'Sin proyecto';
  };

  const getStageName = (stageId) => {
    const stage = stages.find(s => s.id === stageId);
    return stage ? stage.name : 'Sin etapa';
  };

  const handleStatusChange = async (taskId, newStatus) => {
    const result = await dispatch(updateTaskStatus(taskId, newStatus));
    if (result.success) {
      loadData();
    }
  };

  const handleDeleteTask = async (taskId) => {
    if (window.confirm('¬øEst√°s seguro de eliminar esta tarea?')) {
      const result = await dispatch(deleteTask(taskId));
      if (result.success) {
        loadData();
      }
    }
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'Completo':
        return <CheckCircle className="w-4 h-4" />;
      case 'Bloqueado':
        return <AlertTriangle className="w-4 h-4" />;
      case 'Pendiente':
        return <Clock className="w-4 h-4" />;
      default:
        return <Clock className="w-4 h-4" />;
    }
  };

  const getStatusColor = (status) => {
    const colors = {
      'Pendiente': 'bg-gray-100 text-gray-800 border-gray-300',
      'En Dise√±o': 'bg-purple-100 text-purple-800 border-purple-300',
      'En Progreso': 'bg-blue-100 text-blue-800 border-blue-300',
      'Aprobaci√≥n Requerida': 'bg-orange-100 text-orange-800 border-orange-300',
      'Bloqueado': 'bg-red-100 text-red-800 border-red-300',
      'En Discusi√≥n': 'bg-yellow-100 text-yellow-800 border-yellow-300',
      'Completo': 'bg-green-100 text-green-800 border-green-300',
    };
    return colors[status] || 'bg-gray-100 text-gray-800 border-gray-300';
  };

  const taskCounts = {
    all: tasks.length,
    Pendiente: tasks.filter(t => t.status === 'Pendiente').length,
    'En Progreso': tasks.filter(t => t.status === 'En Progreso' || t.status === 'En Dise√±o').length,
    Completo: tasks.filter(t => t.status === 'Completo').length,
    Bloqueado: tasks.filter(t => t.status === 'Bloqueado').length,
  };

  if (loading) {
    return (
      <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center">
        <div className="bg-white rounded-lg p-8">
          <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-blue-600 mx-auto"></div>
          <p className="text-gray-600 mt-4">Cargando tareas...</p>
        </div>
      </div>
    );
  }

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 z-50 flex items-center justify-center p-4">
      <div className="bg-white rounded-xl shadow-2xl w-full max-w-6xl max-h-[90vh] overflow-hidden flex flex-col">
        {/* Header */}
        <div className="bg-gradient-to-r from-blue-500 to-blue-600 px-6 py-4 flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <div className="w-12 h-12 bg-white rounded-full flex items-center justify-center text-blue-600 font-bold text-xl">
              {staff.name.charAt(0).toUpperCase()}
            </div>
            <div className="text-white">
              <h2 className="text-2xl font-bold">{staff.name}</h2>
              <p className="text-blue-100">{staff.role_description || 'Sin rol'}</p>
            </div>
          </div>
          <button
            onClick={onClose}
            className="text-white hover:bg-white hover:bg-opacity-20 rounded-full p-2 transition-colors"
          >
            <X className="w-6 h-6" />
          </button>
        </div>

        {/* Stats Bar */}
        <div className="bg-gray-50 border-b px-6 py-3">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-4">
              <div className="text-sm">
                <span className="font-semibold text-gray-700">Total:</span>
                <span className="ml-2 text-gray-900 font-bold">{taskCounts.all}</span>
              </div>
              <div className="text-sm">
                <span className="font-semibold text-gray-700">Pendientes:</span>
                <span className="ml-2 text-orange-600 font-bold">{taskCounts.Pendiente}</span>
              </div>
              <div className="text-sm">
                <span className="font-semibold text-gray-700">En Curso:</span>
                <span className="ml-2 text-blue-600 font-bold">{taskCounts['En Progreso']}</span>
              </div>
              <div className="text-sm">
                <span className="font-semibold text-gray-700">Completadas:</span>
                <span className="ml-2 text-green-600 font-bold">{taskCounts.Completo}</span>
              </div>
              {taskCounts.Bloqueado > 0 && (
                <div className="text-sm">
                  <span className="font-semibold text-gray-700">Bloqueadas:</span>
                  <span className="ml-2 text-red-600 font-bold">{taskCounts.Bloqueado}</span>
                </div>
              )}
            </div>
          </div>
        </div>

        {/* Filters */}
        <div className="bg-white border-b px-6 py-3 flex items-center space-x-4">
          <Filter className="w-5 h-5 text-gray-600" />
          
          <select
            value={filterStatus}
            onChange={(e) => setFilterStatus(e.target.value)}
            className="border rounded-lg px-3 py-1.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="all">Todos los estados</option>
            {TaskStatusOptions.map(status => (
              <option key={status} value={status}>{status}</option>
            ))}
          </select>

          <select
            value={sortBy}
            onChange={(e) => setSortBy(e.target.value)}
            className="border rounded-lg px-3 py-1.5 text-sm focus:outline-none focus:ring-2 focus:ring-blue-500"
          >
            <option value="status">Ordenar por estado</option>
            <option value="project">Ordenar por proyecto</option>
            <option value="stage">Ordenar por etapa</option>
          </select>
        </div>

        {/* Tasks List */}
        <div className="flex-1 overflow-y-auto p-6">
          {filteredTasks.length === 0 ? (
            <div className="text-center py-12">
              <Clock className="w-16 h-16 text-gray-300 mx-auto mb-4" />
              <p className="text-gray-600">No hay tareas que mostrar</p>
            </div>
          ) : (
            <div className="space-y-3">
              {filteredTasks.map((task) => (
                <div
                  key={task.id}
                  className="bg-white border rounded-lg p-4 hover:shadow-md transition-shadow"
                >
                  <div className="flex items-start justify-between gap-4">
                    {/* Task Info */}
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center gap-2 mb-2">
                        <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-medium border ${getStatusColor(task.status)}`}>
                          {getStatusIcon(task.status)}
                          <span className="ml-1">{task.status}</span>
                        </span>
                        <span className="text-xs text-gray-500 bg-gray-100 px-2 py-1 rounded">
                          {task.category}
                        </span>
                      </div>
                      
                      <h4 className="font-medium text-gray-900 mb-1">
                        {task.task_description}
                      </h4>
                      
                      <div className="flex items-center gap-4 text-sm text-gray-600">
                        <span>üìÅ {getProjectName(task.project_id)}</span>
                        <span>üìã {getStageName(task.stage_id)}</span>
                      </div>
                      
                      {task.notes && (
                        <p className="text-sm text-gray-600 mt-2 line-clamp-2">
                          {task.notes}
                        </p>
                      )}
                    </div>

                    {/* Actions */}
                    <div className="flex items-center gap-2">
                      <select
                        value={task.status}
                        onChange={(e) => handleStatusChange(task.id, e.target.value)}
                        className="text-sm border rounded px-2 py-1 focus:outline-none focus:ring-2 focus:ring-blue-500"
                      >
                        {TaskStatusOptions.map(status => (
                          <option key={status} value={status}>{status}</option>
                        ))}
                      </select>
                      
                      <button
                        onClick={() => handleDeleteTask(task.id)}
                        className="p-2 text-red-500 hover:bg-red-50 rounded transition-colors"
                        title="Eliminar tarea"
                      >
                        <Trash2 className="w-4 h-4" />
                      </button>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </div>

        {/* Footer */}
        <div className="bg-gray-50 border-t px-6 py-4 flex items-center justify-end">
          <button
            onClick={onClose}
            className="px-6 py-2 bg-gray-600 text-white rounded-lg hover:bg-gray-700 transition-colors font-medium"
          >
            Cerrar
          </button>
        </div>
      </div>
    </div>
  );
};

export default StaffTaskModal;
